/* General Reset and Styling */
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
body {
    display: grid;
    justify-content: center;
    align-content: center;
    align-items: center;
    height: 100vh;
    background: linear-gradient(135deg, #1bf8a0, #9945fd);
    background-size: cover;
}

/* Main Container Styling */
.container {
    background: rgba(255, 255, 255, 0.2);
    backdrop-filter: blur(10px);
    padding: 30px;
    border-radius: 15px;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
    width: 350px;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 15px;
}

/* Title */
h1 {
    color: #ffffff;
    font-size: 24px;
    margin-bottom: 20px;
}

/* Button Styles */
button {
    width: 100%;
    padding: 12px;
    margin-top: 10px;
    border: none;
    border-radius: 5px;
    background-color: #28292bde;
    color: white;
    font-size: 16px;
    cursor: pointer;
    transition: background 0.3s;
}

button:hover {
    background-color: #0056b3;
}

/* Wallet Status */
#wallet-status {
    color: #631ec3;
    font-size: 14px;
    margin-top: 10px;
}

/* Label Styles */
label {
    color: #ffffff;
    font-size: 14px;
    margin-top: -3px;
    display: block;
    text-align: center;
    width: 100%;
}

/* Input and Canvas Styles */
input[type="number"], canvas {
    width: 100%;
    padding: 10px;
    margin-top: -14px;
    border-radius: 5px;
    border: 1px solid #ddd;
    font-size: 14px;
}

input::placeholder {
    color: #999;
}

/* Center Container */
body > *:not(script) {
    margin: 20px auto;
}
/* Logo Styling */
.logo {
    width: 100px;
    margin-bottom: 20px;
}

/* Icon Styling Inside Input */
.amount-input {
    display: flex;
    align-items: center;
}

.amount-input .icon {
    width: 24px;
    margin-right: 10px;
}

/* Adjust Input Width to Fit with Icon */
.amount-input input {
    flex: 1;
}
.label-icon {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
}

.label-icon label {
    color: #ffffff;
    font-size: 14px;
    margin-right: 8px; /* Space between label and icon */
}

.label-icon .icon {
    width: 30px; /* Adjust size as needed */
}
select {
    width: 100%;
    padding: 10px;
    margin-top: 10px;
    border-radius: 5px;
    border: 1px solid #ddd;
    font-size: 14px;
    color: #333;
}
/* Style the radio dropdown */
.radio-dropdown {
    display: flex;
    gap: 10px;
}

.radio-dropdown input[type="radio"] {
    display: none;
}

.radio-dropdown label {
    display: flex;
    align-items: center;
    padding: 10px;
    border-radius: 5px;
    background-color: #343d40;
    cursor: pointer;
    transition: background-color 0.2s;
    border: 1px solid #ddd;
}

.radio-dropdown label:hover {
    background-color: #000;
}

.radio-dropdown label .icon {
    width: 20px;
    margin-right: 8px;
}

/* Checked styles */
.radio-dropdown input[type="radio"]:checked + label {
    background-color: #a92bdb;
    border-color: #000;
    color: white;
}
.conversion-section {
    margin-top: 0px;
}

.conversion-section label, .conversion-section input {
    display: block;
    width: 100%;
    margin-top: 10px;
}

#converted-amount {
    background-color: #e0e0e0;
    cursor: not-allowed;
}
/* Styling for links at the bottom of the container */
.links {
    margin-top: 20px;
    text-align: center;
}

.links a {
    color: #ffffff;
    text-decoration: none;
    margin: 0 10px;
    font-size: 14px;
    transition: color 0.3s;
}

.links a:hover {
    color: #a92bdb;
}
#close-canvas {
    margin-top: 10px;
    padding: 8px 16px;
    background-color: #f44336;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s;
}

#close-canvas:hover {
    background-color: #d32f2f;
}
        /* Styling for the keypad layout */
        .keypad {
            display: grid;
            grid-template-columns: repeat(3, 1fr);
            gap: 10px;
            margin: 10px 0;
        }

        .keypad button {
            padding: 15px;
            font-size: 18px;
            border-radius: 8px;
            border: none;
            background-color: #f0f0f0;
            cursor: pointer;
            transition: background-color 0.2s;
        }

        .keypad button:hover {
            background-color: #dcdcdc;
        }

        .keypad button:active {
            background-color: #c0c0c0;
        }

        .keypad .clear {
            grid-column: span 3;
            background-color: #ff6868;
            color: white;
        }